import { UserButton, useUser } from "@clerk/nextjs";
import {
  Home,
  FileText,
  Settings,
  LogOut,
  Search,
  Check,
  FolderOpen,
  Plus,
} from "lucide-react";
import Link from "next/link";
import { motion } from "framer-motion";
import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
